<template>
	<div class="create">
		<!-- <mt-header title="" class="createName">
			<router-link to="/focusFamily" slot="left">
				<mt-button icon="back"></mt-button>
			</router-link>
		</mt-header> -->
		<div class="focus">
			<mt-field label="" placeholder="" v-model="family.appellation" :attr="{ maxlength: 8 }" required></mt-field>
		</div>
		<div class="next" style="margin-top:20px;padding:10px 20px;">
			<mt-button class="next-btn" @click.native="submit"></mt-button>
		</div>
		<div class="next">
			<mt-button class="next-btn delete-btn" @click.native="deleteClick"></mt-button>
		</div>
	</div>
</template>
<script>
	export default {
		data (){
			return {
				family: {
					appellation: this.$route.params.appellation,
					focu_id:this.$route.params.focu_id
				}
			}
		},
		methods:{
			submit () {
				if (this.family.appellation&&this.family.appellation!='') {

						//
						var resource = this.$resource(this.$store.state.serverIP+'/user_focus/edit_focu?'+location.search.substring(1).split("&")[0]);
						resource.save(this.family).then((response) => {
		        			this.$toast({
		        				message: '',
		        				position: 'bottom'
		        			});
		        			setTimeout(() => {
		        				this.$router.go(-1);
		        			}, 1000);
						}, (response) => {
		     				this.$toast(response.data.error);
						})

				}else{
					this.$toast({
						message: '',
						position: 'bottom'
					});
				}
			},
			deleteClick (){

				//
				var resource = this.$resource(this.$store.state.serverIP+'/user_focus/remove_focu?'+location.search.substring(1).split("&")[0]+'&focu_id='+this.$route.params.focu_id);

				this.$confirm('?', '', {
		          	confirmButtonText: '',
		          	cancelButtonText: '',
		          	type: 'warning'
		        }).then(() => {

		         	resource.get().then((response) => {
						this.$message({
		            		type: 'success',
		            		message: '!'
		          		});

						setTimeout(() =>{
							this.$router.push('/focusFamily');
						},1000);
					},(response) =>{
						this.$toast(response.data.error);
					})

		        }).catch(() => {
		          	this.$message({
		            type: 'info',
		            message: ''
		          });
		        });

			}
		}
	}
</script>
<style>
.next .delete-btn{
	background-color: #fff;
	border: 1px solid #000;
	color: #323232;
}
</style>



